.badge {
	composes: badge from './common.m.css';
}

.root {
	position: relative;
	background: hsl(0, 0%, 100%);
	border: 1px solid hsl(0, 0%, 93%);
	padding: 0.2em 1em;
	overflow: hidden
}

.root .badge {
		  background: #33b2cc;
  	}

.root .calendarTimes {
		display: none;
 	}

.root .calendarDay {
		border-top: 1px solid hsl(0, 0%, 92.5%);
	}

.root .calendarDay:first-of-type {
		border: 0;
	}

.root .calendarDayHeader {
		position: relative;
		-webkit-box-flex: 0;
		    -ms-flex: 0 0 4em;
		        flex: 0 0 4em;
		margin-right: 1em;
	}

.root .calendarDate {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: baseline;
		    -ms-flex-align: baseline;
		        align-items: baseline;
		padding: 0.75em 0;
	}

.root .calendarDateMo {
		font-size: 1.5em;
		font-weight: 500;
		line-height: 1;
		margin-right: 0.2em;
	}

.root .calendarDateWk {
		font-size: 0.8em;
		text-transform: uppercase;
		font-weight: 600;
	}

.root .calendarEntries {
		  margin: 0;
		  max-width: 100%;
		  padding: 0;
	}

.root .calendarEntry {
		  border: 1px solid hsl(0, 0%, 75%);
		  border-radius: 0.2em;
		  cursor: pointer;
		  line-height: 1.2;
		  list-style: none;
		  margin: 0 0 1em;
		  padding: 0.5em;
	}

.root .calendarEntryTime {
		  font-size: 0.9em;
		  font-weight: 600;
		  margin-bottom: 0.1em;
	}

.root .calendarEntryDesc {
		  font-size: 0.9em;
	}

/* Medium */

.root.medium .badge {
		background: #ab47bc;
  	}

.root.medium .calendarDay {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
	}

.root.medium .calendarDateMo {
		margin: 0;
	}

.root.medium .calendarEntries {
		-webkit-box-flex: 1;
		    -ms-flex: 1 0 0%;
		        flex: 1 0 0%;
	}

.root.medium .calendarEntry {
		margin: 1em 0;
	}

.root.medium .calendarDate {
		-webkit-box-align: stretch;
		    -ms-flex-align: stretch;
		        align-items: stretch;
		position: -webkit-sticky;
		position: sticky;
		top: 0em;
		left: 0;

		-webkit-box-orient: vertical;

		-webkit-box-direction: normal;

		    -ms-flex-flow: column;

		        flex-flow: column;
		padding: 0.75em 0;
		text-align: center;
	}

.root.medium .calendarDateWk {
		font-size: 0.8em;
		text-transform: uppercase;
		font-weight: 600;
	}

.root.medium .calendarDateMo {
		font-size: 2.5em;
		font-weight: 300;
		line-height: 1;
	}

/* Large */

.root.large .badge {
		background: #40bf80;
	}

.root.large {

	display: -webkit-box;

	display: -ms-flexbox;

	display: flex;
	padding: 1.5em
}

.root.large .calendarTimes {
		display: block;
		margin-top: 4.5em;
		min-width: 4em;
	}

.root.large .calendarTime {

		border-top: 1px solid #eee;
		font-size: calc(1em * 0.8);
		height: calc(3.333em / 0.8);
		list-style: none;
		padding-top: 0.25em;
	}

.root.large .calendarDay {
		-webkit-box-flex: 1;
		    -ms-flex: 1 0 0%;
		        flex: 1 0 0%;
		min-width: 0;

		border: 0;
		border-left: 1px solid #eee;
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		    -ms-flex-flow: column;
		        flex-flow: column;
		padding: 0 1em;
	}

.root.large .calendarDay:last-child {
		padding-right: 0;
	}

.root.large .calendarDayHeader {
		position: static;
	}

.root.large .calendar .calendarDate {
		padding: 0;
		position: static;
		text-align: left;
		height: 4.5em;
		overflow: hidden;
	}

.root.large .calendarEntries {
		position: relative;
	}

.root.large .calendarEntry {
		margin: 0;
		position: absolute;
		width: 100%;
	}

.root.large .calendarEntryDesc {
		font-size: 0.8em;
	}



