.root {
	background: #fff;
	border: 1px solid rgba(0,0,0,.075);
	padding: 1.5em;
	position: relative;
	overflow: hidden;
}

.content {}

.text {
	padding: 0;
	margin: 0;
}

.big .content {
		-webkit-column-count: 3;
		        column-count: 3;
	}

.big .badge {
		background: #40bf80;
	}

.medium .content {
		-webkit-column-count: 2;
		        column-count: 2;
	}

.medium .badge {
		background: #ab47bc;
	}

.small .content {
		-webkit-column-count: 1;
		        column-count: 1;
	}

.small .badge {
		background: #33b2cc;
	}

.title {
	border-bottom: 1px solid rgba(0,0,0,.1);
	font-weight: 700;
	letter-spacing: .15em;
	margin-bottom: 1em;
	padding-bottom: .5em;
	text-transform: uppercase;
	font-size: 1.2em;

}

.root p {
	margin: 0 0 1em;
}

.badge {
	composes: badge from './common.m.css';
}



