.root {
	border: 1px solid rgba(0,0,0,.075);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-flex: row;
	    -ms-flex: row;
	        flex: row;
	padding: 1em;
	position: relative;
	overflow: hidden;
}

.text {
	padding: 0;
	margin: 0;
}

.big .badge {
		background: #40bf80;
	}

.big .badgeLeft {
        background: #40bf80;
    }

.medium {
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-flow: column;
	        flex-flow: column
}

.medium .figureHolder {
		padding-right: none;
		border-right: none;
		margin-bottom: 1em !important;
	}

.medium .badge {
		background: #ab47bc;
  	}

.medium .badgeLeft {
		background: #ab47bc;
  	}

.small .figureHolder {
        display: none;
    }

.small .badge {
		  background: #33b2cc;
 	}

.small .badgeLeft {
		  background: #33b2cc;
	}

.badge {
	composes: badge from './common.m.css';
}

.badgeLeft {
    color: #fff;
    background: #fff;
    font-size: .8em;
    font-weight: 700;
    letter-spacing: .1em;
    position: absolute;
    left: -1.2em;
    text-align: center;
    top: 3.1em;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    width: 6em;
    z-index: 10;
    -webkit-transition: all 0.5s ease-in;
    transition: all 0.5s ease-in;
}

.figure {
	border: 1px solid #e6e6e6;
	padding: .25em;
}

.figure:after {
	background: #e6e6e6;
	content: "";
	display: block;
	height: 0;
	padding-top: 56.25%;
	width: 100%;
}

.figureHolder {
	border-right: 1px solid #eee;
	-webkit-box-flex: 2;
	    -ms-flex: 2;
	        flex: 2;
	margin-bottom: 0;
	margin-right: 1em;
	min-width: 0;
	padding-right: 1em;
}

.title {
	font-size: 1.4em;
	font-weight: 600;
	margin-bottom: .53571em;
}

.bodyHolder {
	-webkit-box-flex: 5;
	    -ms-flex: 5;
	        flex: 5;
	min-width: 0;
}



