.root {
    display: -ms-grid;
    display: grid;
    position: relative;
    overflow: hidden
}

.root > div {
		margin-bottom: 10px;
  	}

.small {
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr
}

.small .badge {
        background: #33b2cc;
    }

.big {
    -ms-grid-columns: 1fr 1fr;
        grid-template-columns: 1fr 1fr
}

.big .badge {
        background: #40bf80;
    }

.badge {
    composes: badge from './common.m.css';
}



